.feature_cards_1_pagePart{font-size:1rem;display:flex;flex-direction:column;justify-content:center;gap:50px}.feature_cards_1_pagePart .cards{display:grid;grid-template-columns:repeat(2, minmax(200px, 1fr));gap:20px}.feature_cards_1_pagePart .card{display:flex;flex-direction:column;justify-content:space-between;position:relative;gap:2rem;background-color:rgba(0,0,0,0);padding:1rem;border-radius:8px;overflow:hidden;z-index:0;min-height:200px}.feature_cards_1_pagePart .card .icon{width:2rem;height:2rem}.feature_cards_1_pagePart .card .text_cont{display:flex;flex-direction:column;gap:10px}.feature_cards_1_pagePart .card .text_cont .tag{font-size:.8em;color:var(--primary-color, teal)}.feature_cards_1_pagePart .card .text_cont .heading{color:var(--first-text-color, white)}.feature_cards_1_pagePart .card .text_cont .description{font-size:.8em;color:var(--second-text-color, #f5f5f5)}